@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Poppins&display=swap);
:root {
  --primary-color: #040c08;
  --secondary-color: #f5f5f5;
  --tersier-color: #ffdf28;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

html {
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif;
}

.content-box {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10vh 10vw;
}

.two-col {
  width: 50%;
  text-align: left;
}

.three-col {
  width: 30%;
  margin: 0 1.5vw;
  text-align: left;
}

@media only screen and (max-width: 960px) {
  .content-box {
    padding: 5vh 5vw 7vh;
  }
  .three-col {
    width: 100%;
  }
  h1 {
    font-size: 1.25em;
  }
  h2 {
    font-size: 1.25em;
  }
  h3 {
    font-size: 1em;
  }
  p {
    font-size: 0.75em;
  }
}

@media only screen and (max-width: 640px) {
  .two-col {
    width: 100%;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header {
  width: 100%;
  height: 60px;
  background: #101010;
  color: #f5f5f5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10vw;
}

header .logo {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 1.5rem;
}

.nav {
  display: flex;
  justify-content: space-between;
}

.nav .nav-list {
  margin-left: 20px;
  cursor: pointer;
  border-radius: 0.25em;
  padding: 0.25em 0.5em;
  transition: all 0.5s ease-in-out;
}

.nav .nav-list:hover {
  background: #f5f5f5;
  color: #101010;
}

main {
  min-height: calc(100vh - 140px);
  width: 100%;
}

.btn {
  padding: 0.5em 1em;
  cursor: pointer;
  margin-right: 10px;
  margin-bottom: 10px;
  display: inline-block;
  text-align: center;
}

.btn.main {
  width: 200px;
  background-color: var(--tersier-color);
  color: var(--primary-color);
  text-transform: uppercase;
  font-weight: 700;
}

@media only screen and (max-width: 540px) {
  .btn.cta {
    width: 100%;
  }
}

.btn.sub-main {
  border: solid 2px var(--tersier-color);
  background: transparent;
  color: var(--tersier-color);
  text-transform: uppercase;
  font-weight: 400;
}

.btn.sub-main:hover {
  background: var(--tersier-color);
  color: var(--primary-color);
  font-weight: 400;
}

.btn-sm {
  font-size: 10px;
}

.btn-md {
  font-size: 16px;
}

.btn-lg {
  font-size: 24px;
}

.btn-primary {
  background-color: var(--primary-color);
  color: var(--secondary-color);
}

.btn-warning {
  background-color: rgb(170, 0, 0);
}

.category {
  display: inline-block;
  padding: 2px 5px;
  background-color: rgb(170, 170, 170);
  color: var(--primary-color);
  border-radius: 2px;
  font-size: 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-weight: 700;
  margin: 0px 5px 10px 0;
  text-align: center;
}

.jumbotron {
  background-image: url(/react-portfolio/static/media/background.6da46748.jpg);
  background-color: rgba(0, 0, 0, 0.5);
  background-blend-mode: multiply;
  flex-direction: row;
  background-size: cover;
  min-height: min(640px, calc(100vh - 60px));
  color: var(--secondary-color);
  padding-top: 20px;
  padding-bottom: 20px;
}

.intro-box {
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 1vw;
  text-align: left;
}

.intro-box h2 {
  margin-bottom: 5px;
}

.intro-box p {
  margin-bottom: 20px;
  letter-spacing: 0.01em;
}

.profile-box {
  display: block;
  margin: auto;
  width: 30vw;
  max-width: 250px;
  aspect-ratio: 1/1;
  overflow: hidden;
  border-radius: 50%;
  margin-bottom: 3vh;
}

.profile-box img {
  display: block;
  margin: auto;
  width: inherit;
  max-width: 250px;
  aspect-ratio: 3/4;
}

.project {
  position: relative;
  flex-direction: column;
  margin-bottom: 10px;
}

.project::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 10px;
  bottom: -10px;
  left: 0;
  background-color: rgba(255, 217, 0, 0.25);
}

.project h1 {
  position: relative;
}

.project h1::after {
  position: absolute;
  bottom: 5%;
  left: -2%;
  content: "";
  background-color: rgba(255, 217, 0, 0.75);
  border-top-right-radius: 15px;
  width: 90%;
  height: 15px;
  z-index: -1;
}

.project .subtitle-content {
  margin-bottom: 3vh;
}

.certificate h1 {
  margin-bottom: 20px;
}

.project-list,
.certificate-list {
  display: flex;
  align-items: center;
  height: 100%;
  border: solid 1px var(--primary-color);
}

.project-list:nth-child(2n),
.certificate-list:nth-child(2n) {
  flex-direction: row-reverse;
}

.project-img-box .project-img,
.cert-img-box .cert-img {
  display: block;
  width: 100%;
  aspect-ratio: 16/9;
}

.project-info,
.cert-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto 0;
  padding: 0 20px;
  position: relative;
  aspect-ratio: 16/9;
  overflow: hidden;
}

.cert-info,
.cert-img-box,
.cert-img-box .cert-img {
  aspect-ratio: 4/3;
}

.cert-info p {
  position: relative;
  margin-bottom: 10px;
}

/* .cert-info p::after {
  content: "";
  height: 5px;
  width: 75%;
  position: absolute;
  background: var(--tersier-color);
  z-index: -1;
  bottom: -10px;
  left: -20px;
} */

.cert-info p::before {
  content: "";
  border-radius: 50%;
  width: 25%;
  aspect-ratio: 1/1;
  position: absolute;
  background: var(--tersier-color);
  z-index: -1;
  bottom: -100%;
  left: 30px;
  opacity: 0.25;
}

.cert-info::after {
  content: "";
  height: 30%;
  width: 5%;
  position: absolute;
  background: var(--tersier-color);
  top: -10px;
  right: 10%;
  -webkit-transform: skewY(30deg);
          transform: skewY(30deg);
  z-index: -1;
  opacity: 0.5;
}

.cert-info::before {
  content: "";
  height: 30%;
  width: 5%;
  position: absolute;
  background: var(--tersier-color);
  top: -10px;
  right: 5%;
  -webkit-transform: skewY(-30deg);
          transform: skewY(-30deg);
  z-index: -1;
  opacity: 0.5;
}

@media only screen and (max-width: 640px) {
  /*
    Certificate
  */
  .project-list,
  .certificate-list {
    margin-bottom: 20px;
    display: block;
    border: 1px solid #f0f0f0;
    overflow: hidden;
    border-radius: 20px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  }

  .project-info,
  .cert-info {
    padding: 30px 20px 30px;
    aspect-ratio: auto;
  }
}

footer {
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  padding: 0 10vw;
  color: #f5f5f5;
  background: #101010;
}

.line {
  margin-left: -10vw;
  width: 60vw;
  height: 1px;
  background-color: #f5f5f5;
  margin-right: 20px;
}

.social-media-icon {
  width: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  aspect-ratio: 1;
  border-radius: 50%;
  overflow: hidden;
  background: var(--secondary-color);
}

.social-media-icon img {
  display: block;
  width: 40px;
  aspect-ratio: 1;
}

